//
// Gravity Forms
//  --------------------------------------------------

.gform_wrapper {
	max-width: 100%!important;
	
	textarea, select, input,  {
		border: 2px solid rgb(238,238,238);	
	}
	
	input[type="radio"]:focus {
		outline:none;
	}
}

body img.gform_ajax_spinner {
   display: none!important;
}

// Custom error handling

.validation_error, .validation_message {
	//display:none;
	border: none!important;
	//color: #FFDFE0!important;
	text-align: left!important;
	padding: 0!important;
	font-weight: 400!important;
}

.top_label .gfield_error {
	width: auto!important;
}

.top_label .gfield_error .ginput_container {
	max-width: 100%;
}

li.gfield.gfield_error, li.gfield.gfield_error.gfield_contains_required.gfield_creditcard_warning {
	background-color: transparent!important;
	margin-bottom: 0!important;
	padding: 0;
	border: none!important;
}

li.gfield.gfield_error input {
	background-color: #FFDFE0!important;
		
}

li.gfield.gfield_error {
	@include placeholder {
		color: #790000;
	}
}



.gform_footer {
	input {
		padding: 6px 30px!important;
		background: $anchor-color;
		color: #fff;
		border: none;
		outline: none;	
 		font-weight: 600;
		border-radius: 6px;
		
		&:hover {
			background: $anchor-color-hover;	
			color: $white;
		}	
	}	
}