}

/* CSS Document */
.template_reference-letters ul.tabs {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0 0 24px -10px;
}

.template_reference-letters ul.tabs:before, .template_reference-letters ul.tabs:after {
  content: " ";
  display: table;
}

.template_reference-letters ul.tabs:after {
  clear: both;
}

.template_reference-letters ul.tabs {
  *zoom: 1;
}

.template_reference-letters ul.tabs li {
  display: inline-block;
  margin: 0 0 10px 10px;
}

.template_reference-letters ul.tabs li a {
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background-color: #d1d2d4;
  color: #fff;
  display: block;
}

.template_reference-letters ul.tabs li a:hover {
  background-color: #666666;
}

.template_reference-letters ul.tabs li.is-active a {
  background-color: #666666;
}

.template_reference-letters .tabs-panel[aria-hidden="false"] {
  display: block;
}

.template_reference-letters .tabs-panel {
  display: none;
}

.template_reference-letters .tabs-panel img {
  margin-bottom: 20px;
}

.template_reference-letters .tabs-panel h3 {
  margin-bottom: 24px;
}

.template_magazines .grid img {
  margin: 0 auto;
}

.template_magazines .grid a:hover {
  opacity: .9;
}

.template_magazines .entry-header {
  padding: 5px 0 30px;
}

.template_magazines .entry-header h3 {
  font-size: 15px;
}

.template_magazines .entry-header h3 a {
  color: #000;
}

/* CSS Document */
.template_testimonials .grid .column, .template_testimonials .grid .columns {
  padding-bottom: 60px;
}

.template_testimonials .grid .quote {
  background: #f5f5f5;
  padding: 15px 30px 30px;
  position: relative;
}

.template_testimonials .grid .quote .thumbnail {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto 24px;
  background-size: cover;
  background-position: center;
}

.template_testimonials .grid .quote:before {
  content: '';
  display: block;
  width: 100%;
  height: 30px;
  background-image: url(assets/images/quote.png);
  background-size: 30px;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  top: -30px;
}

.template_testimonials .grid .details strong {
  display: block;
}
